.App-head {
  height: 40vh;
  background-color: #f5f5f5;
  text-align: center;
  line-height: 1;
}
#topHead {
  font-size: 50px;
  font-family: "Kaushan Script", cursive;
  padding-top: 2%;
}
.subHead {
  font-family: "Raleway", sans-serif;
}
.homePageForm {
  background-color: #f5f5f5;
  padding: 5vh 20%;
}
.goalCard {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  margin: 2.5%;
  opacity: 1;
  float: left;
  padding: 2%;
  width: 350px;
  text-align: center;
  position: relative;
  background-color: #f5f5f5;
}
.goalCard:hover {
  box-shadow: 0 8px 16px 0 #766868;
}

.addNew {
  vertical-align: middle;
  padding-left: 2%;
  color: #38b6eb;
}
.goalCards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.goalInput {
  width: 175px;
  margin-left: 2%;
}
.removeGoal {
  position: absolute;
  top: 2%;
  left: 2%;
  color: #f91313;
}
body {
  background-color: #f0caca;
}
.manageAcctBtn {
  float: right;
}
